export default {
	data() {
		return {
			
		};
	},
	props: {
		resumeData: {
			type: Object,
			default: ()=>({})
		},
	},
	methods:{
		showDetail(){
			const id = this.resumeData.id || 1
			this.$emit('showDetail',id)
		},
		returnEducation(item){
			if(item == null || item == undefined){
				return
			}
			if(item == 0 ){
				return "初中"
			}else if(item == 1){
				return "中专"
			}else if(item == 2){
				return "高中"
			}else if(item == 3){
				return "大专"
			}else if(item == 4){
				return "本科"
			}else if(item == 5){
				return "本科以上"
			}else{
				return
			}
		},
		detailClick(){
			this.$emit('on-click-detail',this.resumeData.id)
		},
		recordClick(){
			this.$emit('on-click-record',this.resumeData.id)
		},
		recommedClick(){
			this.$emit('on-click-recommend',this.resumeData.id)
		},
		callClick(){
			this.$emit('on-click-call')
		}
	}
}
